* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  padding: 0;
  margin: 0;
}

.country-list li {
  display: flex;
}

.country-list p {
  margin: 5px 0;
}

.country-img {
  width: 30px;
  margin-right: 10px;
}

.country-title {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
}
